@import url("../../assets/styles/color.module.css");
@import url("../../assets/styles/fonts.module.css");

.avatarsAndPhone {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 100%;
    margin-bottom: 80px;
}

.iphoneOleg {
    width: 512px;
    height: 540px;
    margin-top: 60px;
    opacity: 0;
    z-index: 0;
    animation: iphoneOlegLoad 0.7s ease-out forwards;
    animation-delay: 2.3s;
}

.avatars {
    position: absolute;
    width: 1432px;
    height: 540px;
    animation-delay: 3s;
    left: 50%;
    transform: translate( -50%, 0);
    opacity: 0;
    animation: avatarLoad 0.8s ease-in-out forwards;
    animation-delay: 3.5s;
}

@keyframes avatarLoad {
    0% {
        opacity: 0;
        width: 1400px;
        height: 510px;
    }
    100% {
        opacity: 1;
        width: 1432px;
        height: 540px;
    }
}

@keyframes iphoneOlegLoad {
    from {
        margin-top: 60px;
        opacity: 0;
    }

    to {
        margin-top: 0;
        opacity: 1;
    }
}

.ponytail, .bang, .pink, .bangMan, .afro, .azure, .redPoint, .glasses {
    width: 200px;
    height: 200px;
    position: absolute;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    border-radius: 50%;
}

