@import url("../../assets/styles/color.module.css");
@import url("../../assets/styles/animation.module.css");
@import url("../../assets/styles/fonts.module.css");

.download {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 210px;
}

.left {
    margin-left: 92px;
}

.left__circle {
    position: relative;
    width: 508px;
    height: 508px;
    border-radius: 50%;
    background-color: var(--c-white);
}

.left__stikerIphone {
    position: absolute;
    width: 320px;
    z-index: 2;
    top: -41px;
    left: 20px;
    animation: stikerIphone 5s ease-in-out infinite;
}

.left__stikerAndroid {
    width: 300px;
    top: -2px;
    right: 21px;
    position: absolute;
    animation: stikerAndroid 5s ease-in-out infinite;
}

.right {
    width: 583px;
    margin-right: 121px;
}

.right__title {
    font-family: Manrope;
    font-size: 40px;
    line-height: 52px;
    color: var(--c-black-blue);
    margin-bottom: 24px;
}

.right__text {
    font-family: ManropeText;
    font-size: 20px;
    line-height: 32px;
    color: var(--c-black-blue);
    margin-bottom: 40px;
}

.right__links {
    display: flex;
    align-items: center;
}

.right__googlePlay {
    width: 162px;
    height: 48px;
    margin-right: 17px;
    background: url("../../assets/img/googlePlay.png");
    background-size: 162px;
}

.right__appStore {
    width: 142px;
    height: 46px;
    background: url("../../assets/img/appStore.png");
    background-size: 142px;
    margin-right: 17px;
}

.right__telegram::after {
    content: "";
    position: absolute;
    width: 5px;
    height: 5px;
    border-right: 2px solid var(--c-white);
    border-bottom: 2px solid var(--c-white);
    transform: rotate(-45deg);
    right: 21px;
    top: 19.5px;
    background: transparent;
}

.right__telegram {
    position: relative;
    width: 192px;
    padding: 7px 20px 7px;
    font-size: 20px;
    line-height: 32px;
    background: var(--c-brand);
    color: var(--c-white);
    border-radius: 12px;
    font-family: Manrope;
    transition: .5s;
}

.right__telegram:hover {
    background: var(--c-brand-hover);
    transition: .5s;
}

.right__telegram::before {
    content: "";
    position: absolute;
    width: 12px;
    height: 2px;
    right: 22px;
    top: 50%;
    transform: translate(0%, -50%);
    background: var(--c-white);
}

@keyframes stikerIphone {
    from {
        top: -41px;
    }

    50% {
        top: -47px;
    }

    to {
        top: -41px;
    }
}

@keyframes stikerAndroid {
    from {
        top: -2px;
    }

    50% {
        top: 4px;
    }

    to {
        top: -2px;
    }
}