@import url("../../assets/styles/color.module.css");
@import url("../../assets/styles/fonts.module.css");

.bigBgCircle {
    position: absolute;
    z-index: -1;
    top: -3675px;
    left: 50%;
    transform: translate(-50%, 0);
    background: var(--c-white);
    border-radius: 50%;
    width: 4400px;
    height: 4400px;
    animation: bigBgCircleLoad 0.5s ease-in-out forwards;
    animation-delay: 2s;
}

@keyframes bigBgCircleLoad {
    from {
        background: var(--c-white);
        top: -3675px;
    }

    to {
        background: var(--c-body-bg);
        top: -3575px;
    }
}

.loadModal {
    z-index: 9;
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: var(--c-white);
    display: none;
    transition: .5s;
}

.loadModalAvatar {
    display: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-repeat: no-repeat;
    background-position: center bottom;
    width: 120px;
    height: 120px;
    z-index: 10;
    transition: .5s;
    animation: load 1.5s step-end forwards;
    border-radius: 50%;
    border: 5px solid var(--c-brand);
}

@keyframes load {
    0% {
        background-image: url("../../assets/img/faces/pink.png");
        background-size: 70%;
        border: 5px solid var(--c-brand);
    }

    7% {
        background-image: url("../../assets/img/faces/bang.png");
        background-size: 60%;
        border: 5px solid var(--c-brand);
    }

    14% {
        background-image: url("../../assets/img/faces/redPoint.png");
        background-size: 60%;
        border: 5px solid var(--c-brand);
    }

    22% {
        background-image: url("../../assets/img/faces/ponytail.png");
        background-size: 60%;
        border: 5px solid var(--c-brand);
    }

    29%, 36%, 48% {
        background-image: url("../../assets/img/faces/glasses.png");
        background-size: 65%;
        width: 120px;
        height: 120px;
        border: 5px solid var(--c-brand);
        animation-timing-function: ease-in-out;
    }

    60%, 80% {
        background-image: url("../../assets/img/faces/glasses.png");
        background-size: 65%;
        width: 200px;
        height: 200px;
        animation-timing-function: ease-in-out;
        border: 0px solid transparent;
    }

    100% {
        background-image: url("../../assets/img/faces/glasses.png");
        animation-timing-function: ease-in-out;
        background-size: 65%;
        width: 120px;
        height: 120px;
        border: 0px solid var(--c-brand);
    }
}

.hero {
    padding-top: 232px;
}

.heroText {
    color: var(--c-black-blue);
    width: 942px;
    margin: 0 auto 84px auto;
}

.heroText__title {
    font-family: Manrope;
    font-size: 56px;
    line-height: 72px;
    letter-spacing: -2px;
    text-align: center;
    margin-bottom: 24px;
    animation: heroTitleLoad ease-in-out 0.5s;
    animation-delay: 1.3s;
    opacity: 0;
}

@keyframes heroTitleLoad {
    from {
        opacity: 0;
        padding-top: 100px;
        color: var(--c-white);
    }

    top {
        padding-top: 0;
        opacity: 1;
        color: var(--c-black-blue);
    }
}

.heroText__text {
    font-family: ManropeText;
    font-size: 24px;
    line-height: 36px;
    text-align: center;
    animation: heroTextLoad var(--heroText) ease-in-out;
}

@keyframes heroTextLoad {
    from, 50% {
        color: var(--c-white);
        padding-top: 50px;
    }

    to {
        color: var(--c-black-blue);
        padding-top: 0;
    }
}